import { profileApi } from "../api/api";

const ADD_POST = 'ADD_POST';
const SET_USER_PROFILE = 'SET_USER_PROFILE';
const SET_STATUS = 'SET_STATUS';
const SET_PHOTO = 'SET_PHOTO';

let initialState = {
	posts: [
		{
			id: 1,
			message: 'Hello',
			ava: 'data:image/png;base64,'
		}
	],
	profile: null,
	status: '',
	image: ''
};

const profileReducer = (state = initialState, action) => {
	switch (action.type) {
		case ADD_POST:
			let newPost = {
				id: 1,
				message: action.postText,
				ava: 'data:image/jpeg;base64,'
			}
			return {
				...state,
				posts: [...state.posts, newPost]
			}

		case SET_USER_PROFILE:
			return {
				...state,
				profile: action.profile
			}
		case SET_STATUS:
			return {
				...state,
				status: action.status
			}
		case SET_PHOTO:
			return {
				...state,
				image: 'https://st.joinsport.io/player/2174946/photo/5de7d76b38157_thumb.jpg'
			}
		default:
			return state;
	}
}

export const addPost = (postText) => ({ type: ADD_POST, postText });
export const setUserProfile = (profile) => ({ type: SET_USER_PROFILE, profile });
export const setStatus = (status) => ({ type: SET_STATUS, status });
export const setPhoto = (image) => ({ type: SET_PHOTO, image });

export const getProfile = (userId) =>
	async (dispatch) => {
		let data = await profileApi.getProfile(userId);
		dispatch(setUserProfile(data));
	}

export const getStatus = (userId) =>
	async (dispatch) => {
		let data = await profileApi.getStatus(userId);
		dispatch(setStatus(data));
	}

export const updateStatus = (status) =>
	async (dispatch) => {
		let data = await profileApi.updateStatus(status);
		if (data.resultCode === 0) {
			dispatch(setStatus(status));
		}
	}

export const updatePhoto = (image) =>
	async (dispatch) => {
		let data = await profileApi.updatePhoto(image);
		if (data.resultCode === 0) {
			dispatch(setPhoto(image));	
		}
		
	}

export default profileReducer;